





























import {showToast,request} from 'common/js/common'
import {placeOrderAndPay} from 'common/js/requestUrl'
export default {
	data() {
		return {
			name: '',
			phone: '',
			province: '',
			city:'',
			county:'',
			address: ''
		};
	},
	onLoad(){

	},
	methods: {
		chooseAddress(){
		  uni.chooseAddress({
			success:(res) => {
				console.log('success')
			  this.name = res.userName
			  this.province = res.cityName
			  this.city = res.cityName
			  this.county = res.countyName
			  this.address = res.detailInfo
			  this.phone = res.telNumber
			},
			fail(res){
				console.log(res,'fail')
			},
			complete(res){
				console.log(res, 'complete')
			}
		  })
		},
		placeOrderAndPay(){
			request(placeOrderAndPay,{
				name: this.name,
				phone: this.phone,
				province: this.province,
				city: this.city,
				country: this.country,
				address: this.address
				},(res) => {
					if(res.code != 200){
						showToast('生成订单错误');
					}
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: res.result.timeStamp,
						nonceStr: res.result.nonceStr,
						package: res.result.package,
						signType: res.result.signType,
						paySign: res.result.paySign,
						success: function (res) {
							console.log('success:' + JSON.stringify(res));
						},
						fail: function (err) {
							console.log('fail:' + JSON.stringify(err));
						}
					});
				}
			)
		},
		submit(){
			if(!this.address){
				showToast('请先选择地址')
				return
			}
			this.placeOrderAndPay()
		}
	}
}
